import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import RedesGeneral from 'components/redes-general/redes-general'
import data from 'data/aboutUs.json'
import redesData from 'data/home.json'
import heroesData from 'data/heroes.json'
import Hero from './hero/Hero'
import './AboutUs.css';


class AboutUs extends Component {
  render() {
    const { t } = this.props;
    return (
      <div className="AboutUs">
        <div className="hero">
          <img className="hero-img" alt="" src={data.data.puntosSuperiores} />
          <img className="hero-img-medium" alt="" src={data.data.puntosSuperioresMedio} />
          <p >{t(data.data.name)}</p>
        </div>

        <div className="team-photo">
          <img className="team-photo-img" alt="" src={data.data.photoColor} />
          <img className="team-photo-img-medium" alt="" src={data.data.photoColorMedio} />
        </div>

        <div className="aboutUs-description">
          <div className="description">
            <p className="description-title">{data.data.name}</p>
            <div className="container-description-details">
              <p className="description-details">{t(data.data.description)}</p>
            </div>
          </div>
          <div className="philosophy">
            <p className="philosophy-text">{t(data.data.textTitle)}</p>
            <p className="philosophy-detail">{t(data.data.text)}</p>
            <img className="philosophy-logo" alt="" src={data.data.logo} />
            <p className="philosophy-description">{t(data.data.textDescription)}</p>
          </div>
        </div>
        <p className="philosophy-text ourheroes">{t("about.heroesTitle")}</p>
        <div className="Heroes-container">
          {
            heroesData.heroes.map(
              (hero, index) => <Hero
                  key={index}
                  index={index}
                  marcoMobile={heroesData.marcoMobile}
                  marco1Desktop={heroesData.marco1Desktop}
                  marco2Desktop={heroesData.marco2Desktop}
                  lineas={heroesData.lineas}
                  marcoText={heroesData.marcoText}
                  hero={hero}
                />)
          }
        </div>
        <div className="padder">
          <RedesGeneral redesData={redesData} />
        </div>
      </div>
    );
  }
}

export default withTranslation()(AboutUs);